import AbstractController from './../../../base/controller/Abstract.controller.js';
import LazyImageLoader from './../../../services/LazyImageLoader.service';
import Swiper2 from './../../../../../local-packages/sensory-slider/src/slider';
import $ from 'jquery';
import dom from 'fasterdom';

const sliderOpts = {
	slideSpeed :                      400,
	autoplay :                        5000,
	classNameFrame :                  'slider-container',
	classNameSlideContainer :         'slider-wrapper',
	classNameSlide :                  'slider-slide',
	classNameActiveSlide :            'active',
	classNameNextCtrl :               'slider-next',
	classNamePrevCtrl :               'slider-prev',
	classNamePaginationContainer :    'slider-pagination',
	classNamePagination :             'swiper-pagination-bullet',
	classNameActivePagination :       'swiper-pagination-bullet-active',
	classNameEnablePaginationEvents : 'swiper-pagination-clickable',
	classNameReady :                  'slider--ready'
};

/**
 *
 */
/**
 * Executed after slider init
 * @returns {void}
 */
function onReady () {
	fasterdom.mutateInstant( () => {
		this.$scope.addClass( 'slider--ready' );
		LazyImageLoader.update();
	} );
}

/**
 * Class representing a slider instance.
 * Controls the state of the slider navigation.
 * Controls slider behavior.
 * @extends { AbstractController }
 */
class SliderController extends AbstractController {

	constructor () {
		super();

		this.$scope = null;

		this.$slider = null;
		this.slider  = null;

		this.opts = $.extend( true, {}, sliderOpts );
	}

	/**
	 * Initialises the controller.
	 * Has to be called after ready event.
	 *p
	 * @param { object } $root - jQuery wrapped DOM node
	 * @returns { void }
	 * @public
	 */
	init ( $root ) {
		const self = this;

		this.$scope = $root;

		// self.opts.onInit = onReady.bind( this );

		dom.measureInstant( () => {
			if ( !self.$scope.hasClass( 'sli01--autoplay' ) ) {
				self.opts.autoplay = false;
			}

			self.$slider = self.$scope.find( '.slider-container' );
			self.slider  = new Swiper2( self.$scope, self.opts );

			// patch for .sta01--compact
			if ( this.$scope.find( '.sta01--compact' ).length > 0 ) {
				dom.measureInstant( () => {
					this.$scope.addClass( 'sli01--has-compact' );
				} )
			}
		} );
	}
}

/**
 * @public
 * @return {SliderController} SliderController class
 */
export default SliderController;



