export default {

	/**
	 * array index of initial active slide
	 * if no valid value is set ( slide array index )
	 * either the first slide with 'classNameActiveSlide'
	 * or the very first slide will be set active
	 * @activeSlide {Null|Number}
	 */
	activeSlide : null,

	/**
	 * slides scrolled at once
	 * @todo: currently multiple slide scroll is not supported
	 * @slidesToScroll {Number}
	 */
	slidesToScroll : 1,

	/**
	 * time in milliseconds for the autoplay timeout
	 * if value is not positive number autoplay is deactivated
	 * @autoplay {Number}
	 */
	autoplay : 0,

	/**
	 *
	 * @responsive {Boolean}
	 */
	responsive : true,

	/**
	 * time in milliseconds for the animation of a valid slide attempt
	 * @slideSpeed {Number}
	 */
	slideSpeed : 500,

	/**
	 * time in milliseconds for the animation of the rewind after the last slide
	 * @rewindSpeed {Number}
	 */
	rewindSpeed : 600,

	/**
	 * time for the snapBack of the slider if the slide attempt was not valid
	 * @snapBackSpeed {Number}
	 */
	snapBackSpeed : 200,

	/**
	 * Basic easing functions: https://developer.mozilla.org/de/docs/Web/CSS/transition-timing-function
	 * cubic bezier easing functions: http://easings.net/de
	 * @ease {String}
	 */
	ease : 'ease',

	/**
	 * if slider reached the last slide, with next click the slider goes back to the startindex.
	 * use infinite or rewind, not both
	 * @rewind {Boolean}
	 */
	rewind : true,

	/**
	 * number of visible slides or false
	 * use infinite or rewind, not both
	 * @infinite {Boolean}
	 */
	infinite : true,

	/**
	 * class name for slider ready state
	 * will be added after slider initialized
	 * @classNameReady {String}
	 */
	classNameReady : 'slider--loaded',

	/**
	 * class name for slider frame
	 * @classNameFrame {String}
	 */
	classNameFrame : 'slider__frame',

	/**
	 * class name for slides container
	 * @classNameSlideContainer {string}
	 */
	classNameSlideContainer : 'slider__container',

	/**
	 * class name for slides
	 * @classNameSlide {String}
	 */
	classNameSlide : 'slider__slide',

	/**
	 * class name for current active slide
	 * if emptyString then no class is set
	 * @classNameActiveSlide {String}
	 */
	classNameActiveSlide : 'slider__slide--active',

	/**
	 * class name for slider prev control
	 * @classNamePrevCtrl {string}
	 */
	classNamePrevCtrl : 'slider__prev',

	/**
	 * class name for slider next control
	 * @classNameNextCtrl {string}
	 */
	classNameNextCtrl : 'slider__next',

	/**
	 * class name for slider pagination container
	 * @classNamePaginationContainer {String}
	 */
	classNamePaginationContainer :     'slider__pagination',

	/**
	 * class name for slider pagination item
	 * @classNamePagination {String}
	 */
	classNamePagination : 'slider__pagination-bullet',

	/**
	 * class name for current active pagination item
	 * if emptyString then no class is set
	 * @classNameActivePagination {String}
	 */
	classNameActivePagination : 'active',

	/**
	 * tag name for slider pagination item
	 * if emptyString then no class is set
	 * @tagNamePagination {String}
	 */
	tagNamePagination :  'div',

	/**
	 * class name to enable slider pagination events
	 * @classNameEnablePaginationEvents {String}
	 */
	classNameEnablePaginationEvents : 'slider__pagination--clickable',

	/**
	 * show/hides slider pagination menu
	 * @enablePagination {Boolean}
	 */
	enablePagination : true,

	/**
	 * enables click events for slider pagination items
	 * @enablePaginationEvents {Boolean}
	 */
	enablePaginationEvents : true,

	/**
	 * enables mouse events for swiping on desktop devices
	 * @enableMouseEvents {boolean}
	 */
	enableMouseEvents : true,

	/**
	 * window instance
	 * @window {object}
	 */
	window : window,


};



