import BaseModule from './../Base.module';
import SliderController from './controller/Slider.controller';

/**
 * @extends {BaseModule}
 */
class SliderModule extends BaseModule {

	constructor ( config ) {

		super( config );
	}

	init () {
		super.init();

		const controllerInstance = new SliderController();

		this.slider = controllerInstance.init( this.node );
	}
}

export default SliderModule;



