	const { infinite }  = options,
	      duration      = this.touchOffset ?
	                      Date.now() - this.touchOffset.time :
	                      0,
	      slideForward  = this.delta.x < 0,
	      isOutOfBounds = this._activeSlide === 0 && !slideForward || this._activeSlide === this._lastSlide && slideForward;

