	this.frame.removeEventListener( 'touchmove', _onTouchMove );
	this.frame.removeEventListener( 'touchend', _onTouchEnd );
	this.frame.removeEventListener( 'mousemove', _onTouchMove );
	this.frame.removeEventListener( 'mouseup', _onTouchEnd );
	this.frame.removeEventListener( 'mouseleave', _onTouchEnd );
